<template>
    <div class="updateNews">
      <el-form  :model="formData" >
        <el-form-item label="新闻标题">
          <el-input v-model="formData.title"></el-input>
        </el-form-item>
        <el-form-item label="新闻描述">
          <el-input v-model="formData.desc"></el-input>
        </el-form-item>
        <el-form-item label="新闻类型">
          <el-select v-model="formData.type" placeholder="请选择类型">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="新闻图片">
          <el-upload
            class="avatar-uploader"
            action="https://upload-z1.qiniup.com"
            :show-file-list="false"
            :on-success="upload"
            :data="{token:token}"
          >
            <img v-if="formData.img" :src="formData.img" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="新闻内容">
          <editor v-model="formData.content" :isChange="editorInit"></editor>  <!--局部注册的组件在这里使用-->
        </el-form-item>
        <el-form-item>
          <el-button type="primary" round @click="submit">保存</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>
<script>
  import editor from '../../components/wangeditor.vue'
    export default{
      name: "update",
      components:{
        editor
      },
      data(){
        return{
          formData:{
            title:'',
            desc:'',
            type:'',
            img:'',
            content:''
          },
          editorInit: false,
          token:'',
          options: [],
        }
      },
      methods:{
        //数据回填
        getData() {
          this.$axios.get('getNews',{id: this.$route.query.id}).then(res => {
            this.formData = res.data
          })
        },
        //获取新闻类型
        getType(){
          this.$axios.get('getNewsCate').then( res => {
            this.options = res.data
            console.log(this.options)
          })
        },
        getToken(){
          this.$axios.qiniuGet().then(res => {
            this.token = res.data
          })
        },
        upload(res){
          this.formData.img = res.url
        },
          //修改用户
        submit(){
          let params = {
            ...this.formData,
            id:this.$route.query.id
          }
          this.$axios.post('updateNews',params).then( res => {
            if(res.code == 200){
              this.$message({
                message:`${res.msg}`,
                type:'success'
              })
            }else{
              this.$message({
                message:`${res.msg}`,
                type:'warning'
              })
            }
            setTimeout( () => {
              this.$router.push('/News')
            },1000)
          })
        }
      },
      created(){
        this.getToken();
        this.getData();
        this.getType()
      }
    }
</script>
<style scope>
  .updateNews{
    width: 100%;
    height:100%;
    margin-right: 22px;
    padding-left:22px;
    padding-top: 20px;
  }
  /*表单上传*/
  .avatar-uploader{
    display: inline-block;
  }
  .avatar-uploader .el-upload{
    width: 190px;
    height: 190px;
    border:1px solid #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
  }
  .avatar-uploader-icon{
    font-size: 28px;
    color: #8c939d;
    width: 190px;
    height: 190px;
    line-height: 190px;
    text-align: center;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  /*设置上传图片的大小*/
  .avatar {
    width: 190px;
    height: 190px;
    display: block;
  }
  /*底部*/
  .updateNews .el-footer{
    /*position: fixed;*/
    /*min-height: 500px;*/
    /*bottom: 20px;*/
    /*right:20px;*/
    position:relative;
    height: 56px !important;
    margin:0 22px 0 0;
    padding:0;
    background-color: #f1f1f1 !important;
  }
  .updateNews .el-button{
    margin:10px 0 0 10px;
  }
  .updateNews .el-footer .el-pagination{
    display: inline-block;
    position:absolute;
    top:12px;
    right:15px;
  }
  .updateNews .el-footer .el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
    background-color: #ffffff;
  }
</style>



